import React, { useState,useEffect } from "react";
import { useHistory } from "react-router-dom";
import orderModel from "../model/orderModel";
import {Table,Typography} from "antd"
import type { ColumnsType } from "antd/es/table";
import dayjs from "dayjs"
let {Text} =Typography
interface DataType {
  _id:string,
  orderid: String; //订单编号
  partaid: {partaname:string};
  partbid: string;
  productname: String; //产品名称
  productmodel: String; //产品型号
  productcolor: String; //颜色 后期变成type 常量设置的的
  productlight: String; //光源
  productdirection: String; //方向
  productsize: Number; //规格(mm)
  productnum: Number; //数量
  productprice: Number; //产品价格,
  date: Date;
}
export default function Print() {
  let history = useHistory();
  let [list, setList] = useState([]);
  let [partaid,setpartaid] = useState<any>("")
  let [partbid,setpartbid] = useState<any>("")
  let [orderid,setorderid] = useState<any>("")
  let [fax,setfax] = useState<any>("")
  let [tel,settel] = useState<any>("")
  let [date,setdate] = useState<any>("")
  useEffect(()=>{
    orderModel.list().then((res) => {
      let list = res.data.msg.filter((item: any) => {
        return item.orderid === history.location.search.slice(1).split("=")[1];
      });
      setList(list);
      setpartaid(list[0]?.partaid.partaname)
      setpartbid(list[0]?.partbid.partbname)
      setorderid(list[0]?.orderid)
      setfax(list[0]?.partbid.fax)
      settel(list[0]?.partaid.tel)
      setdate(list[0]?.partaid.date)
    });
  // if (history.location.search) {
    
    },[])
    
  // } else {
  //   history.push("/home");
  // }
  const columns: ColumnsType<DataType> = [
    {
      title: "序号",
      render:(_,record,index)=>{
        return <>{index+1}</>
      }
    },
    {
      title: "产品名称",
      dataIndex: "productname",
    },
    {
      title: "产品型号",
      dataIndex: "productmodel",
    },
    {
      title: "表面处理(颜色)",
      dataIndex: "productcolor",
    },
    {
      title: "光源",
      dataIndex: "productlight",
    },
    {
      title: "出线方向",
      dataIndex: "productdirection",
    },
    {
      title: "规格(mm)",
      dataIndex: "productsize",
    },
    {
      title: "数量(支)",
      dataIndex: "productnum",
    },
    {
      title: "单价(元/米)",
      dataIndex: "productprice",
    },
    {
      title: "金额(元)",
      // dataIndex: "productprice",
      render:(_,record)=>{
        return <>{(Number(record.productprice)*Number(record.productnum)*Number(record.productsize)/1000).toFixed(2)}</>
      }
    },
  ];
  return (
    <div style={{ padding: "0 60px" }}>
      <h1 style={{ textAlign: "center" }}>佛山市二十七装饰材料有限公司</h1>
      <h2 style={{ textAlign: "center" }}>订单确认书</h2>

      <p>
        <span>订货方(甲方):{partaid}</span>
        <span style={{ float: "right", width: "300px" }}>
          订单：{orderid}
        </span>
      </p>
      <p>
        <span>电 话：{tel}</span>
        <span style={{ float: "right", width: "300px" }}>日期：{dayjs(date).format("YYYY年MM月DD日")}</span>
      </p>
      <p>
        <span>出货方(乙方)：{partbid}</span>
        <span style={{ float: "right", width: "300px" }}>传真：{fax}</span>
      </p>
      <p style={{paddingTop:"20px"}}>兹因甲方向乙方订购下列产品，经双方议妥条款如下，以资共同遵守。</p>
      <p>一、货品名称、数量、金额等如下：</p>
      <Table
        style={{maxHeight:"100%",overflowY:"auto"}}
        columns={columns}
        dataSource={list}
        scroll={{ x: 1024}}
        rowKey="_id"
        bordered
        pagination={false}
        summary={(pageData) => {
          return (
            <>
              <Table.Summary.Row>
                <Table.Summary.Cell index={0}>合计</Table.Summary.Cell>
                <Table.Summary.Cell index={1}>
                {/* type="danger" */}
                  <Text >{
                    list.length?list.map((item:any)=>{
                      return (Number(item.productprice)*Number(item.productnum)*Number(item.productsize)/1000)
                    }).reduce((a,b)=>{return a+b}).toFixed(2)+"元":"暂无"

                  }</Text>
                </Table.Summary.Cell>
              </Table.Summary.Row>
            </>
          );
        }}
      />
    </div>
  );
}
